import React,{useEffect} from "react";
import "./App.css";
import NewsTable from "./components/NewsTable";

function App() {

  let domain = "http://dm2grig.mart-shop.ru";
  let path = "/wp-admin/admin-ajax.php";
  let ajaxUrl = process.env.NODE_ENV == "development" ? domain + path : path;

  const [data, setData] = React.useState({
    loading: false,
    result: { news: ["1", "2"] },
  });

  useEffect( ()=> {console.log('useEffect:',data)});



  function dgLoader() {
    // { data.result.news.map( nw => <p>{nw}</p>)}
    setData({ loading: true, result: { news: ["Загрузка..."] } });

    fetch(ajaxUrl + "?action=test")
      .then((res) => res.json())
      .then((res) => {
        setData({ loading: false, result: res });
        console.log('fetch:',data);
      });
  }
  return (
    <div className="App">
      <p>result:{process.env.NODE_ENV}</p>
      <button onClick={dgLoader}>load</button>
      <p>status:{data.loading ? "loading..." : ""}</p>
      <h2>Новости</h2>
      <ul>
      {data.result.news.map((nw, index) => (
        <li key={index}>{nw}</li>
      ))}
      </ul>
    </div>
  );
}

export default App;
